
.channels-grid {
  margin: 0 auto;

  @media (max-width: 700px) {
    .category-title {
      display: none;
    }

    .categories {
      padding: 6px 0 0;
      background-color: rgba(#000, 0.2);
    }
  }

  h5 {
    text-align: center;
    font-size: 26px;
    font-weight: bold;
    letter-spacing: 2px;
    padding: 20px 0 40px 6px;
  }

  .channels-grid-box {
    display: flex;
    flex-wrap: wrap;
  }

  .channel {
    max-width: 120px;
    width: 30%;
    margin: 0 2% 10px;
    padding: 4px;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;

    .channel-icon {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 10px;
      width: 100%;
      min-width: 30px;

      >img {
        position: absolute;
        z-index: 1;
        width: 100%;
        overflow: hidden;
        border-radius: 100%;
      }

      .bg-placeholder {
        padding-top: 100%;
        width: 100%;
        background-color: #0057b7;
        color: #fff;
        overflow: hidden;
        border-radius: 100%;

        p {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          font-size: 46px;
          font-weight: 600;
        }
      }

      .active {
        position: absolute;
        background-color: #fff;
        border-radius: 100%;
        width: 28px;
        bottom: 5px;
        overflow: hidden;
        right: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 2px solid #fff;
        z-index: 2;

        img {
          width: 30px;
        }
      }
    }

    p {
      font-size: 12px;
      text-align: center;
      line-height: 20px;
    }

    .left-box {
      width: 100%;
    }

    .right-box {
      display: none;
    }

    .channel-name {
      text-align: center;
    }

    @media (max-width: 700px) {
      flex-direction: row;
      justify-content: space-between;
      max-width: 100%;
      width: 100%;
      padding: 10px 10px;
      border-bottom: .4px solid rgba(rgb(199, 199, 199), 0.9);
      margin: 0;

      &:last-child {
        border-bottom: none;
      }

      .left-box {
        display: flex;
        align-items: center;
      }

      .right-box {
        display: flex;
      }

      .channel-icon {
        width: 30px;
        margin-bottom: 0;

        .bg-placeholder {
          p {
            font-size: 20px;
          }
        }

        .active {
          display: none !important;
        }
      }

      .channel-name {
        font-size: 14px;
        margin-left: 10px;
        text-align: left;
        padding-right: 10px;
      }
    }
  }
}
