
.settings {
  min-height: 100vh;
  color: #000;

  .q-field--auto-height.q-field--dense.q-field--labeled
    .q-field__control-container {
    padding-top: 0px;
  }

  .back-button {
    margin-left: 6px;
  }

  .setting-title {
    font-size: 32px;
    font-weight: bold;
    letter-spacing: 2px;
    padding: 0 0 20px 6px;

    &:first-child {
      padding-top: 20px;
    }
  }

  .dark-mode-toggle-btn {
    margin: 0 0 0 6px;
  }

  .setting-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px 10px;
    margin: 0 0 20px 0;
    max-width: 320px;

    .setting-label {
      font-size: 20px;
      font-weight: bold;
      letter-spacing: 2px;
    }
  }

  @media (max-width: 700px) {
    padding-top: 0;

    .setting-item {
      max-width: 100%;
    }
  }
}

.settings-loader {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 80px);
  img {
    width: 50px;
  }
}
