

.np-outer-switch {
  width: 50px;
  height: 24px;
  border-radius: 30px;
  background: rgb(155, 155, 155);
  cursor: pointer;
  position: relative;
}

.np-outer-switch-off {
  background: rgb(141, 141, 141);
  transition: background 0.4s;
}

.np-outer-switch-on {
  background: #009300;
  transition: background 0.4s;
}

.np-inner-switch {
  height: 24px;
  width: 24px;
  background: #fff;
  border-radius: 50%;
  position: absolute;
  top: 0px;
  left: 0px;
}

.np-inner-switch-left {
  top: 0px;
  left: 0px;
  transition: left 0.4s;
}

.np-inner-switch-right {
  top: 0px;
  left: 26px;
  transition: left 0.4s;
}
